<template>
            <el-scrollbar tag="ul"   wrap-class="el-select-dropdown__wraps" view-class="el-select-dropdown__list">
                <div class="logo___2J9hf" >
                    <a href="#/">
                    <img src="data:image/svg+xml;base64," alt="logo">
                    <h1 style="color:#fff">vue-express</h1>
                    </a>
                </div>
                <el-menu mode="vertical" router :default-active="$route.path" :collapse="false" >
                <node-content :node="item"  v-for="(item,index) in routerz" :key="index"></node-content>
                </el-menu>
            </el-scrollbar>
  
</template>

<script>

import { mapGetters } from 'vuex'
import SidebarItem from './Sidebaritem.vue'

export default {
  data() {
    return {
      routerz:[]
    }
  },
  components: { 
      SidebarItem,
      NodeContent: {
        props: {
          node:null
        },

        render(h) {
          const datas = this.node;
          const loop = (data) => data.map((item,index) => {
            if (item.children && item.children.length) {
                return (<el-submenu index={item.path} >
                <template slot="title">{item.name}</template>
                {loop(item.children)}
            </el-submenu>)
            }else{
                return (<el-menu-item index={item.path} >
                {item.name}
              </el-menu-item>)
            }
          });
          return (<el-submenu index={datas.path } >
              <template slot="title">
                <i class="el-icon-menu"></i>
                <span>{datas.name}</span>
              </template>
              { datas.children  ? loop(datas.children) :''}
            </el-submenu> );
        }
      }
  
  },
  computed: {
    isCollapse() {
      return !this.sidebar.opened
    }
  },
  created() {
    this.routerz = this.$router.options.routes
    console.log(this.routerz)
  },
}
</script>
<style rel="stylesheet/scss" lang="scss" scoped>
.logo___2J9hf {
    height: 64px;
    position: relative;
    line-height: 64px;
    padding-left: 24px;
    -webkit-transition: all .3s;
    transition: all .3s;
    background: #002140;
    overflow: hidden;
}
.logo___2J9hf h1, .logo___2J9hf img {
    display: inline-block;
    vertical-align: middle;
}
.logo___2J9hf img {
    height: 32px;
}

</style>
<style>
.el-select-dropdown__wraps {

 height: 102%;
}
.el-select-dropdown__list{
    padding: 0;
}
</style>



