<template>
    <div >
        <el-table
        :row-class-name="tableRowClassName"
        :data="formdata"
        style="width: 100%">
        <el-table-column
            prop="index"
            label="序号"
            width="160">
            <template slot-scope="scope">
                <span >{{ scope.$index + 1 }}</span>
            </template>
        </el-table-column>
        <el-table-column prop="name" label="部门名称" >
            <template slot-scope="scope">
                <div :style="{'paddingLeft': scope.row.type != '4' ? (Number(scope.row.type)-1)*30+'px' : ''}">
                    <span style="display:inline-block;width:14px;height:14px;">
                        <i style="cursor: pointer;" class="el-icon-caret-right" @click="rowclick( scope.row, scope.column, scope.$index )"  v-if="scope.row.children && !scope.row.flag"></i>
                        <i style="cursor: pointer;" class="el-icon-caret-bottom" @click="cellclick( scope.row, scope.column, scope.$index )"  v-if="scope.row.children && scope.row.flag"></i>
                        
                    </span>
                    {{scope.row.name}}
                </div>
            </template>
        </el-table-column>
        <el-table-column
            prop="type"
            label="部门类型"
            width="100">
            <template slot-scope="scope">
                <span>{{otype[scope.row.type - 1]}}</span>
            </template>
        </el-table-column>
        <el-table-column
            label="操作"
            width="200"
            >
        </el-table-column>
        </el-table>
    </div>
</template>

<script>
var istitution = 1;
var nstitution = 1;
var flags = true;
export default {
  name: 'personal',
  data() {
    return {
        columns: [
          {
            text: '部门名称',
            dataIndex: 'name'
          },
          {
            text: '部门类型',
            dataIndex: 'type'
          }
        ],
      optiontext:'',
      Checkedx:false,               //是否分校
      addbranchnum:1,
      labelPosition:'right',
      dialogVisible:false,
      form:{
        orgid:'',
        orgname:'',
        parentOrgname:'',
        parentOrgid:'',
      },
      data2: [],
      data3: [],
      formdata: [],
      tabledata:[{
          label: '一级 1',
          children: [{
            label: '二级 1-1',
            children: [{
              label: '三级 1-1-1'
            }]
          }]
        }, {
          label: '一级 2',
          children: [{
            label: '二级 2-1',
            children: [{
              label: '三级 2-1-1'
            }]
          }, {
            label: '二级 2-2',
            children: [{
              label: '三级 2-2-1'
            }]
          }]
        }, {
          label: '一级 3',
          children: [{
            label: '二级 3-1',
            children: [{
              label: '三级 3-1-1'
            }]
          }, {
            label: '二级 3-2',
            children: [{
              label: '三级 3-2-1'
            }]
          }]
        }],
      defaultProps: {
        children: 'children',
        label: 'label'
      },
      otype: ['学校','部门','部门','分校区'],
      ishandlcontrol:[],    //权限分配
      nohandlcontrol:[],
    }
  },
  components:{
    
    
  },
  created(){
    this.loadTree();
  },
  mounted() {

  },
  computed: {
    nohandlcontrols:function(){
      let _nohandlcontrol = this.nohandlcontrol.map(function(item,index){
            return item.id
      })
      return _nohandlcontrol
    },
    ishandlcontrols:function(){
      let _ishandlcontrol = this.ishandlcontrol.map(function(item,index){
            return item.id
      })
      return _ishandlcontrol
    }
  },
  methods:{
    rowclick(row, column, $index){
        // console.log(row, column, $index)
        let _this=this;
        this.formdata[$index].flag=true;
        row['children'].forEach((r,index)=>{
        _this.formdata.splice($index+1+index,0,r)
        })
        
       // console.log(this.formdata)
    },
    cellclick(row, column, $index){
        this.formdata[$index].flag=false;
        function flagchange(rs){
        rs.forEach(r=>{
            if(r['children']){
                flagchange(r['children'])
            }
            r.flag=false
        })
        
        }
        flagchange(this.formdata[$index]['children'])
        let _this=this;
        
        function del(rs){
        let arr=[]
        rs.forEach((r,index)=>{
                        if(r['children']){
            /* console.log(arr.concat(del(r['children']))) */
            arr=arr.concat(del(r['children']))
            // console.log(arr)
            /* arr.concat(del(r['children'])) */
            }
            arr.push(r.id) 
        })
        // console.log(arr)
        return arr
        }
        // console.log(del(row['children']),'1')
        let arrdata=del(row['children'])
        let data=[]
        this.formdata.forEach((r,index)=>{
        if(arrdata.indexOf(r.id) == -1){
            data.push(r)
        }
        })
        this.formdata=data
    },
    loadTree() {
        let tabledata = this.tabledata
        function flagchange(rs){
            rs.forEach((r,index)=>{
                if(r['children']){
                    flagchange(r['children'])
                }
                r['flag']=false;
            })
        }
        flagchange(tabledata)
        this.formdata=tabledata
        if (type == 'table') {
        } else {
          this.data2 = d;
          this.data3 = d;
        }
    },
  },
     
}

</script>
<style scoped>
.roleassign{
  height:100%;
  text-align: center;
}
.log-magess{
  margin:6px auto 16px;
  position: relative;
  background-color:#fff;
  height:50px;
  width:1200px;
  color:#000;
  font-size:16px;
}
.log-magess .arrow-center{
  display:inline-block;
  margin-left:24px;
  height:46px;
  border-bottom:4px solid #208af5;
  line-height:50px;
}
.log-magess .arrow-right{
    position: absolute;
    right:10px;
    top:16px;
    cursor: pointer;
}
.magessBox{
  width:1200px;
  margin:0 auto 30px;
  padding-bottom:20px;
  background-color:#fff;
}
.tabs{
  height:40px;
  background-color:#eef1f6;
  text-align:left;
  overflow: hidden;
}
.tabs-items{
  margin-left:-1px;
  border-bottom:1px solid #dfe6ec;
  height:39px;
}
.tabs-items li{
  float:left;
  width:120px;
  line-height:39px;
  height:39px;
  text-align:center;
  font-size:14px;
  cursor:pointer;
}
.tabs-items-item{
  background-color:#fff;
  border-right:1px solid #dfe6ec;
  border-top:1px solid #dfe6ec;
  border-left:1px solid #dfe6ec;
  color:#208af5;
}
.roleassign-table{
  width:1150px;
  margin:20px auto 0;
}
.roleassign-right{
  width:880px;
}
.roleassign-right-title{
  text-align:left;
  height:20px;
}
.roleassign-right-title img{
  cursor:pointer;
}
.addschool-f{
  text-align:right;
}
.addschool-f img{
  cursor:pointer;
}
</style>


